import React from 'react';
import VersionPromptBox from '../components/Modal/VersionPromptBox/VersionPromptBox';
import Feedback from '../components/Modal/Feedback/Feedback';

const SplitterBetaFeature = props => (
  <div>
    <VersionPromptBox
      isBrowserVersionOK
      siteId={props.siteId}
      agentId={props.agentId}
    />
    <Feedback
      maxFileSize={5}
      siteId={props.siteId}
      agentId={props.agentId}
    />
  </div>
);

SplitterBetaFeature.propTypes = {
  siteId: React.PropTypes.number,
  agentId: React.PropTypes.number,
};

export default SplitterBetaFeature;



