import React, { PropTypes } from 'react';
import classnames from 'classnames';
import Modal from '../Modal';
import style from './versionPromptBox.css';
import Link from '../../Link/Link';
import banner from '../../../components/Image/versionSwitchBanner.png';
import arraw from '../../../components/Image/arraw-min.png';

import { getCookie, setCookie } from '../../../utils/helper';

class VersionPromptBox extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      open: (getCookie(`ACBetaUsed_${props.siteId}_${props.agentId}`) === ''),
    };
    this.closeModal = this.closeModal.bind(this);
  }

  componentDidMount() {
    setCookie(`ACBetaUsed_${this.props.siteId}_${this.props.agentId}`,
    'true',
     365);
  }

  closeModal() {
    this.setState({
      open: false,
    });
  }

  render() {
    return this.state.open && <Modal
      closeModal={this.closeModal}
      className={style.versionPrompt}
    >
      { this.props.isBrowserVersionOK &&
      (<div className={style.contaner}>
        <div className={style.banner}> <img src={banner} alt="" /></div>
        <div className={classnames(style.content, style.blockTop)}>
          <span>
            You are now using the new beta version of Comm100 Live Chat Agent Console.
            If you encounter any problems or have any suggestions, please let us know
            by clicking the Feedback button in the lower left corner.
          </span></div>

        <div className={classnames(style.content, style.blockTop)}>
          <span>Why am I using the new beta version?</span></div>
        <div className={style.content}>
          <span>1. The new beta version is based on HTML5; while the old version
            is based on Flash. Main browsers, such as Chrome and Firefox, are
            stopping their full support for Flash; while all browsers support HTML5.<br />
        2. The new beta version has the exact same features as the old version.
          </span></div>
        </div>)}

      { !this.props.isBrowserVersionOK &&
      (<div>
        <div className={style.contaner}>
          <div className={style.banner}> <img src={banner} alt="" /></div>
          <div className={classnames(style.content, style.blockTop)}>
            <span>We failed to switch you to the new beta version of Comm100 Live Chat Agent Console.
              Please upgrade your browser or change to another browser.
            </span></div>

          <div className={classnames(style.content, style.blockTop)}>
            <span>Why should I switch to the new<sup>beta</sup> version?</span></div>
          <div className={style.content}>
            <span>1. The new beta version is based on HTML5; while the old version is based on Flash.
              Main browsers, such as Chrome, Firefox and IE, have stopped their full support for
              Flash; while all browsers support HTML5.<br />
          2. The new beta version has the exact same features as the old version.
            </span></div>

          <div className={classnames(style.content, style.blockTop)}>
            <span>For your convenience, we have got you here the download link of the latest
              <Link>Chrome</Link> and <Link>Firefox</Link></span></div>
        </div>
        <div className={classnames(style.bottomInfo, style.blockTop)}>
          <span>I’ll do it later.
            <Link>Back to the old version for now.</Link>
          </span>
        </div>
        </div>)}


      <div className={style.arrow}><img alt="" src={arraw} /></div>
    </Modal>;
  }
}

export default VersionPromptBox;

VersionPromptBox.propTypes = {
  siteId: PropTypes.number.isRequired,
  agentId: PropTypes.number.isRequired,
  isBrowserVersionOK: PropTypes.bool,
};



