import React, { PropTypes } from 'react';
import axios from 'axios';
import classnames from 'classnames';
import style from './feedback.css';
import Icon from '../../Icon/Icon';
import { getOSInfo, getBrowserInfo, getResolution } from '../../../utils/common';
import { YesOrNo, compareResult, icons as enums } from '../../../constants/enums';
import RadioButton from '../../RadioButton/RadioButton';
import Button from '../../Button/Button';
import { portalLivechatUrl } from '../../../constants/login';
import { supportPlaceholder } from '../../../utils/browser';


class FeedBack extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      isViewForm: false,
      smooth: 0,
      interface: 0,
      hasProblem: 0,
      problem: '',
      hasFile: false,
      fileName: '',
      errorInfo: '',
      txtAreaFocus: false,
      submitOK: false,
      OSInfo: getOSInfo(),
      browser: getBrowserInfo(),
      resolution: getResolution(),
    };
    this.updateViewFormStatus = this.updateViewFormStatus.bind(this);
    this.deleteFile = this.deleteFile.bind(this);
    this.udpateSmooth = this.udpateSmooth.bind(this);
    this.udpateInterface = this.udpateInterface.bind(this);
    this.udpateHasProblem = this.udpateHasProblem.bind(this);
    this.submitFeedback = this.submitFeedback.bind(this);
    this.attarchFileClick = this.attarchFileClick.bind(this);
    this.updateInitStatus = this.updateInitStatus.bind(this);
  }

  componentDidUpdate() {
    if (this.txtArea) {
      setTimeout(this.updateInitStatus, 0);
    }
  }

  onfileChange() {
    this.tempFiles = this.upload.files; // .files
    const hasFile = this.upload.files.length > 0;
    const fileName = hasFile ? this.upload.files[0].name : '';
    this.setState({
      hasFile,
      fileName,
      errorInfo: '',
    });
  }
  updateInitStatus() {
    supportPlaceholder(this.txtArea, 'Type your problems here');
  }
  deleteFile() {
    this.setState({
      hasFile: false,
      errorInfo: '',
    });
  }
  updateViewFormStatus(view) {
    this.setState({
      isViewForm: view,
    });
  }
  udpateSmooth(val) {
    this.setState({
      smooth: val,
    });
  }
  udpateInterface(val) {
    this.setState({
      interface: val,
    });
  }
  udpateHasProblem(val) {
    this.setState({
      hasProblem: val,
    });
  }
  udpateProblemInfo(tex) {
    this.setState({
      problem: tex,
    });
  }
  updateTextAreaStatus(focus) {
    this.setState({
      txtAreaFocus: focus,
    });
  }
  attarchFileClick() {
    if (this.upload) { this.upload.click(); }
  }

  submitFeedback() {
    // 参考： https://github.com/mzabriskie/axios/blob/master/examples/upload/index.html
    const data = new FormData();
    if (this.state.hasFile && this.tempFiles[0]) {
      const file = this.tempFiles[0];
      if (file.size >= this.props.maxFileSize * 1024 * 1024) {
        this.setState({
          errorInfo: `The max file size is ${this.props.maxFileSize} MB.`,
        });
        return;
      }
      data.append('file', file);
    }
    data.append('smooth', this.state.smooth);
    data.append('interface', this.state.interface);
    data.append('problem', this.state.problem
      === 'Type your problems here' ? '' : this.state.problem);
    data.append('agentId', this.props.agentId);
    data.append('siteId', this.props.siteId);
    data.append('OSInfo', this.state.OSInfo);
    data.append('browser', this.state.browser);
    data.append('resolution', this.state.resolution);

    axios.post(`${portalLivechatUrl}/FeedbackHandler.ashx`, data, {})
    .then(() => {
      this.setState({
        submitOK: true,
      });
      setTimeout(() => {
        this.updateViewFormStatus(false);
        this.setState({
          submitOK: false,
          smooth: 0,
          interface: 0,
          hasProblem: 0,
          problem: '',
          hasFile: false,
          errorInfo: '',
        });
      }, 2000);
    })
    .catch((err) => {
      this.setState({
        errorInfo: `${err.message}.`,
      });
    });
  }

  render() {
    return (
      <div className={classnames(style.Container, this.state.isViewForm ? style.fromContainer : style.buttonTransform)} >
        {!this.state.isViewForm && (
        <div
          className={style.button}
          onClick={() => { this.updateViewFormStatus(true); }}
        />)}

        {this.state.isViewForm && (<div className={style.formContainer} >
          <div className={style.formhead}>
            <Icon
              type={enums.close}
              className={style.close}
              onClick={() => { this.updateViewFormStatus(false); }}
            />
            Feedback
          </div>
          <div className={style.form}>
            <div>
              <div className={style.noteInfo}>How do you compare the smoothness of the new beta version to the old version?</div>
              <div>
                <RadioButton
                  className={style.singleSelect}
                  radioOption={
                  [{ value: compareResult.better.toString(),
                    text: 'The new beta version is smoother than the old version.',
                    checked: this.state.smooth === compareResult.better,
                  }, {
                    value: compareResult.same.toString(),
                    text: 'The new beta version is almost the same as the old version in terms of smoothness.',
                    checked: this.state.smooth === compareResult.same,
                  }, {
                    value: compareResult.worse.toString(),
                    text: 'The new beta version is less smooth than the old version.',
                    checked: this.state.smooth === compareResult.worse,
                  }]
                      }
                  handleChange={(event) => {
                    this.udpateSmooth(+(event.target.value));
                  }}
                  name="feedback-smooth"
                  horizontal={false}
                />
              </div>
            </div>

            <div>
              <div className={style.noteInfo}>How do you compare the interface of the new beta version to the old version?</div>
              <div>
                <RadioButton
                  className={style.singleSelect}
                  radioOption={
                  [{ value: compareResult.better.toString(),
                    text: 'I prefer the interface of the new beta version. ',
                    checked: this.state.interface === compareResult.better,
                  }, {
                    value: compareResult.same.toString(),
                    text: 'I am okay with the interface of both the new beta and old versions.',
                    checked: this.state.interface === compareResult.same,
                  }, {
                    value: compareResult.worse.toString(),
                    text: 'I prefer the interface of the old version. ',
                    checked: this.state.interface === compareResult.worse,
                  }]
                      }
                  handleChange={(event) => {
                    this.udpateInterface(+(event.target.value));
                  }}
                  name="interface"
                  horizontal={false}
                />
              </div>
            </div>
            <div>
              <div className={style.noteInfo}>
                Have you encountered any problems?
              </div>
              <div>
                <RadioButton
                  className={style.singleSelect}
                  radioOption={
                  [{ value: YesOrNo.yes.toString(),
                    text: 'Yes',
                    checked: this.state.hasProblem === YesOrNo.yes,
                  }, {
                    value: YesOrNo.no.toString(),
                    text: 'No',
                    checked: this.state.hasProblem === YesOrNo.no,
                  }]
                    }
                  handleChange={(event) => {
                    this.udpateHasProblem(+(event.target.value));
                  }}
                  name="problems"
                  horizontal
                />
              </div>
              <div
                className={classnames(
                style.problemInfo,
                this.state.txtAreaFocus ? style.txtareafocus : '',
                this.state.hasProblem === YesOrNo.yes ? style.txtareaenabled : style.txtareadisbled)}
              >
                <textarea
                  ref={(element) => { this.txtArea = element; }}
                  className={classnames(style.textarea,
                  this.state.hasProblem === YesOrNo.yes ?
                   style.txtareaenabled : style.txtareadisbled)}
                  // placeholder="Type your problems here"
                  onChange={(ele) => { this.udpateProblemInfo(ele.target.value); }}
                  value={this.state.problem}
                  readOnly={this.state.hasProblem !== YesOrNo.yes}
                  onFocus={() => { this.updateTextAreaStatus(this.state.hasProblem === YesOrNo.yes); }}
                  onBlur={() => { this.updateTextAreaStatus(false); }}
                />
                <div className={classnames(style.cover, this.state.hasProblem === YesOrNo.yes ? style.dn : '')} />
                <span className={classnames(style.fileattarched, this.state.hasFile ? '' : style.dn)}>
                  <Icon
                    type={enums.sendFile}
                  />
                  <span
                    className={style.fileName}
                  >
                    {this.state.fileName}
                  </span>
                  <Icon
                    type={enums.close}
                    className={style.fileDelete}
                    onClick={() => { this.deleteFile(); }}
                  />

                </span>


                <input
                  className={style.dn}
                  id="tempFile" type="file"
                  ref={(element) => { this.tempFile = element; }}
                />
                {!this.state.hasFile && (<input
                  className={style.dn}
                  id="file" type="file"
                  onChange={() => { this.onfileChange(); }}
                  ref={(element) => {
                    this.upload =
                    (this.state.hasProblem === YesOrNo.yes ? element : null);
                  }}
                />)}
                <Icon
                  type={enums.sendFile}
                  className={classnames(style.fileattarch, this.state.hasFile ? style.dn : '')}
                  onClick={() => { this.upload && this.upload.click(); }}
                  disabled={this.state.hasProblem !== YesOrNo.yes}
                />
              </div>
              <div
                className={
                classnames(style.success, this.state.submitOK ? '' : style.dn)}
              >
                submit success!
              </div>
              <div
                className={
                classnames(style.error, this.state.errorInfo === '' ? style.dn : '')}
              >
                Error: {this.state.errorInfo}
              </div>
              <div className={style.submit}>
                <Button
                  text="Submit"
                  htmlType="submit"
                  className={style.submitButton}
                  onClick={this.submitFeedback}
                />
              </div>
            </div>
          </div>
          </div>)}
      </div>);
  }
}

export default FeedBack;

FeedBack.propTypes = {
  agentId: PropTypes.number,
  siteId: PropTypes.number,
  maxFileSize: PropTypes.number,
};



